// Page header at the top of the page, showing the forum name, post name, search icon, hamburger icon, and user avatar.
.header-buttons {
  display: none;
}

.d-header {
  height: $header-height;
  padding-top: 0;
  border-bottom: $border-style-thin $color-border-light;
  box-shadow: none;

  @include small-browser-width {
    border-top: $border-style-thin $color-border-light;
  }

  @media all and (min-width: $max-banner-width) {
    &:before {
      display: block;
      content: " ";
      width: 100%;
      height: 40px;
      position: absolute;
      background-color: rgba(255, 255, 255, 0.05);
    }
  }

  // Outer wrapper.
  .wrap {
    height: 100%;
  }

  // Inner wrapper.
  .contents {
    @include flex;
    height: 100%;
    margin: 0;
  }

  // Breadcrumbs in the navbar.
  .b-breadcrumbs {
    display: flex;
    overflow: hidden;
    //override Blizzard UI font size
    font-size: $font-down-2;

    // Each breadcrumb icon in the navbar.
    .b-breadcrumb {
      @include text-with-icon($iconSize: 1em, $marginAmount: 0.5em);
      @include clickable-item($color: $color-text-priority-2);
      font-size: $font-0;
      margin-right: 0.5em;

      &:last-child {
        color: $color-text;
      }

      // Right-pointing chevron in front of every breadcrumb except for the home.
      .d-icon-blizzard-chevron-right {
        color: $color-text-priority-4;
      }
    }

    @include small-browser-width {
      .b-breadcrumb-home {
        padding-left: 0.8em;
      }
    }

    // House icon.
    .d-icon-blizzard-home {
      color: $color-theme-blizzard;
    }

    // Don't truncate home/category, truncate the title
    .b-breadcrumb-home,
    .b-breadcrumb-category {
      flex-shrink: 0;
    }

    // Hide extra breadcrumbs when on mobile
    .b-breadcrumb-category,
    .b-breadcrumb-topic {
      @include small-browser-width {
        display: none;
      }
    }

    .b-breadcrumb-topic {
      span {
        align-items: center;
        @include text-nowrap-truncate-ellipsis;

        img.emoji {
          margin-right: 0.25em;
        }
      }
    }
  }

  // Icon and name of the entire forum (i.e. "Overwatch", "World of Warcraft", "Discourse", etc).
  .title {
    height: 100%;
    margin-right: 0.5em;
    padding-right: 1em;
    border-right: $border-style-thin $color-border-light;

    // Hide the forum title in lieu of icon only for small browsers
    @include small-browser-width {
      padding-right: 0;
      margin-left: 0.5em;
      .b-forum-title {
        display: none;
      }
    }

    // Element that holds the name component of the forum name.
    a {
      @include text-with-icon($iconSize: 1.7em, $marginAmount: 0.1em);
      @include clickable-item($color: $color-text-priority-2);
      height: 100%;

      &:visited {
        color: $color-text-priority-2;
      }
    }
  }

  // Outer wrapper div for search icon, hamburger icon, and avatar icon.
  .panel,
  &.scroll-down .panel {
    @include flex("none");
    order: 1; // Place it to the right of everything else.
    margin-left: auto;
    @include small-browser-width {
      margin-right: 0.6em;
    }
  }

  .d-header-icons .badge-notification {
    top: 4px;
  }

  // Parent element for the icons inside the panel.
  .icons {
    @include flex;
    margin-left: 0;

    // An individual icon.
    .icon {
      @include clickable-item;
      border: none;
    }

    // Actual icon inside the .icon element.
    .svg-icon {
      display: block;
      padding: 5px;
      // Set the width and height to 1em and control the size of the icon using font-size.
      width: 1em;
      height: 1em;
      font-size: $font-up-1;
      @include small-browser-width {
        font-size: $font-up-3; // Larger tap targets on small screens
      }
    }

    @include small-browser-width {
      .header-dropdown-toggle {
        margin-left: $small-space; // Additional space beteween icons for small screens
      }
    }

    #current-user .icon {
      margin-left: 7px; // Add some additional spacing so that the spacing between the 3 icons looks the same.

      & img {
        transition: filter $transition-hover-speed;
        // Make the icon brighter on hover.
        &:hover {
          filter: brightness(1.2);
        }

        // Make the icon darker on click.
        &:active {
          filter: brightness(0.8);
        }
      }

      // Parent div for the unread private messages and unread notification bubbles.
      div {
        position: relative;
      }

      // Common class for the unread private message and unread notifications.
      .badge-notification {
        top: 0.1em;
        transform: translate(-50%, 0);
        padding: 3px 5px;
      }

      // Unread private messages notification. This one displays on the top left corner of the avatar.
      .unread-private-messages {
        left: 0;
        top: 0.1em;
        right: auto;
      }

      // Unread notifications. This one displays on the top right corner of the avatar.
      .unread-notifications {
        right: auto;
        background: $Blizzard-blue-base;
      }

      .avatar {
        box-shadow: 0 0 0 1px rgba(255, 255, 255, 0.3), 0 0 0 2px #000;
      }
    }
  }

  .d-header-icons .icon:hover,
  .d-header-icons .icon:focus {
    &,
    & .d-icon {
      background-color: transparent;
    }
  }

  .d-header-icons .header-dropdown-toggle {
    display: flex;
  }
}

// Icon styling when it's clicked and the dropdown is visible.
.drop-down-mode .d-header-icons .active .icon {
  @include clickable-item;
  border: none;
  background-color: transparent;

  // Prevent the badge notification (circle with the number in it) from shrinking when the dropdown menu is open.
  .flagged-posts {
    right: unset;
  }

  &::after {
    display: none;
  }
}
