// Share link that displays when you click on a timestamp in the individual topic view.
#share-link.visible {
  @include b-box($gradient: true);
  // Post count.
  h3 {
    margin: 0;
    line-height: normal;
  }

  // Date the post was created.
  .date {
    color: $color-text-priority-3;
    font-size: $font-down-1;
    margin: 0 0.325em 0 auto;
  }

  // Textbox for the post link.
  input[type="text"] {
    width: 100%;
    margin: 0.1em 0 0.4em;
  }

  // Twitter, Facebook, and e-mail icons.
  .social-link {
    margin-left: 0;
    font-size: $font-up-1;
  }

  // New Topic link.
  .reply-as-new-topic {
    margin-left: 0;

    a {
      @include text-with-icon;
      color: $color-theme-blizzard;
      &:hover {
        color: $color-button-flat-text-hover;
      }
    }
  }

  // Close icon.
  .link {
    margin-left: auto;
    font-size: $font-0;

    a {
      @include clickable-item;
    }
  }
  .social-link .d-icon {
    fill: $color-theme-blizzard;
    &:hover {
      fill: $color-button-flat-text-hover;
    }
  }
}
.share-topic-modal .modal-body .sources {
  button.notify,
  button.invite {
    display: none;
  }
}
