// Preference categories shown on the left column on the user profile, preferences page.
.preferences-nav {
  // Hide the profile and e-mails categories. They do not apply because we have a customized account view.
  .nav-profile,
  .nav-emails {
    display: none;
  }
}

// Battle.net account external link control group.
.b-account-link {
  // External link itself.
  .battle-net-link {
    @include text-with-icon($iconPosition: "after", $marginAmount: 0.75em);

    // External link icon.
    .d-icon-external-link {
      top: 0.1em; // Push the icon down slightly to vertically align with the text better.
    }
  }
}

.about .controls {
  .btn {
    color: $color-text-priority-2;
    .d-icon {
      transition: $transition-hover-button;
      fill: $color-button-tertiary-text;
    }
    &:hover {
      color: $color-button-text-hover;
      .d-icon {
        fill: $color-button-tertiary-text-pressed;
      }
    }
  }
}

// User preferences shown on the right column on the user profile, preferences page.
.user-preferences {
  // Major category name. There can either be a single entry under it, or a group of controls.
  .control-label {
    font-weight: normal;
    // Use margins to control spacing, not line height.
    line-height: normal;
    margin-bottom: 0.9em;
  }

  // Parent element for a label and its control.
  .controls {
    line-height: normal;
    margin-bottom: 0.2em;
  }

  // Static text that can't be interacted with.
  .static {
    color: $color-text-priority-2;
  }

  // Instructions that are displayed below some controls.
  .instructions {
    color: $color-text-priority-3;
  }

  // Parent element for a label and a dropdown control. Only used in the Notifications category.
  .controls-dropdown {
    margin-bottom: 1em;
  }

  // Make the select dropdowns take up more space for a more consistent layout.
  .select-kit {
    min-width: 20em;
  }

  // Reduce the left margin of the desktop notification button so that the button text looks visually centered.
  .desktop-notifications .btn-icon-text {
    padding-left: 2.5em;
  }

  // Don't show Discourse's default avatar selector in user's preferences
  .pref-avatar {
    display: none;
  }

  // Don't show Discourse's default associated accounts section
  .pref-associated-accounts {
    display: none;
  }

  .pref-avatar-blizzard .avatar {
    @include avatar-regular-glow-only;
    width: 50px;
    height: 50px;
    // Ensure there's enough space between the avatar and the 'change avatar' button
    margin-right: 0.5em;
  }

  // The button to change a user's name
  .change-name {
    margin-bottom: 2em;
  }

  .ignored-list {
    color: #909090;
    background-color: rgba(0, 0, 0, 0.3);
    padding: 3px;
    padding-bottom: 5px;
    .ignored-user-list-item {
      border: 0px;
      border-radius: initial;
      background: var(--primary-low);
      color: #fff;
      margin: 2px 0 0 3px;
      button {
        color: var(--primary-low-mid);
        border: 0px;
        background-color: transparent;
        &:hover {
          color: var(--danger);
        }
      }
    }
    border: 1px solid rgba(255, 255, 255, 0.15);
  }
  .user-ignore {
    .btn {
      color: $color-text-priority-2;
      .d-icon {
        transition: $transition-hover-button;
        fill: $color-button-tertiary-text;
        padding-right: 7px;
      }
      &:hover {
        color: $color-button-text-hover;
        .d-icon {
          fill: $color-button-tertiary-text-pressed;
        }
      }
    }
  }
  .select-kit.multi-select {
    background-color: rgba(0, 0, 0, 0.3);
  }
  .select-kit.multi-select .choices {
    padding: 3px;
  }
  .select-kit.multi-select .choices .choice.input-wrapper {
    min-width: 100%;
  }
  .select-kit .select-kit-filter {
    width: 100%;
    background-color: transparent;
  }
  .select-kit.multi-select .multi-select-header {
    border: 1px solid rgba(255, 255, 255, 0.15);
    background-color: transparent;
    &:hover {
      border: 1px solid rgba(255, 255, 255, 0.3);
    }
  }
  label .d-icon {
    color: var(--tertiary);
  }
  .input-xxlarge {
    width: 20em;
  }
}
