a.b-subcategory {
  @include b-transbox;
  align-items: center;
  padding: 0.9em 1.5em;
  color: $color-text-priority-1;

  max-width: 350px;

  font-size: $font-0;
  margin-bottom: 2em;

  .category-icon img {
    width: 30px;
    height: 30px;
    margin-right: 0.5em;
  }

  @include breakpoint(mobile-extra-large) {
    margin-bottom: -1px;
    max-width: unset;
  }
}
