.search-container {
  @include compensate-padding;
  margin-top: $large-space;

  // required for IE to not make checkboxes look weird
  input[type="checkbox"] {
    height: auto;
    min-height: auto;
  }

  .search-bar {
    .search-cta {
      padding: $large-space;
    }

    .search-query {
      //fix size for pixel perfect alignment with call to action button
      height: 50px;
      padding: auto;
    }
  }

  @include very-small-browser-width {
    .search-advanced .search-title .bulk-select {
      padding: 9.5px;
      margin-right: $small-space;
      margin-left: 0;
    }

    .fps-select {
      margin-right: $small-space;
    }
  }

  @include large-browser-width {
    // tweak for icon only button size
    .bulk-select {
      padding: 9.5px;
    }
  }

  .result-count {
    // locale is not updated to use <span> like core yet, to remove after
    .term {
      margin-left: 0.25em;
    }
  }

  .search-header {
    &,
    .search-filters,
    .search-bar,
    .search-filters .search-advanced-filters {
      background: none;
    }
  }
}

.google-search-form {
  margin-top: $large-space;
}

#google-search {
  //appears when no results found
  @include flex(none);
  .btn-primary {
    padding: $medium-space $large-space; //button is too wide on small devices
  }
  input {
    flex-grow: 1; //input will fill full row
    margin-bottom: 0;
    @include large-browser-width {
      max-width: $screen-xs-min;
    }
  }
}

.search-menu {
  .no-results .d-icon {
    display: inline-block;
    margin-left: 0.5em;
  }
}
