// Topic title that displays at the top of the post view. Will also show any icons and also the category the post
// belongs to.
#topic-title {
  @include compensate-padding;
  .title-wrapper {
    margin-bottom: $large-space;

    .category-chooser {
      margin-bottom: $large-space;
    }
  }
  // Large topic name with the icons before and after it (if any).
  h1 {
    @include typography-heading-h1; // Override Discourse's size of 1.2em. Need to use the mixin for responsiveness.
    font-weight: bold;
    line-height: normal;

    // Everything within the h1 is wrapped in an <a> (icons, text, etc).
    a {
      color: $color-text;
      &.topic-status {
        color: $color-theme-primary;
      }
    }
    .topic-status {
      color: $color-theme-primary;
    }
  }

  // Pencil icon to edit the topic name. Only shows for admin users.
  .edit-topic {
    font-size: var(--font-down-3);
    margin-left: 0.7em;
  }

  // Icons before the topic name.
  .topic-statuses {
    .d-icon {
      color: $color-theme-primary;
    }
    margin-top: 0;
    font-size: $font-down-2;
    align-self: flex-start;
    margin-top: 0.5em;
    @include small-browser-width {
      //hiding topic status from title on mobile
      display: none;
    }
  }

  // Category name and icon that's displayed below the topic name.
  .topic-category .badge-wrapper {
    display: block;
    font-size: $font-0;
    margin: 0.25em 0;
  }
}
