div.page-not-found {
  display: none; //hiding the default Discourse error message
}
h1.page-not-found {
  text-align: center;
}

@include small-browser-width {
  .not-found-container {
    padding: 0 1em;
  }
}

.error-message {
  text-align: center;
  margin: 5em 0 8em 0;
  .error-type {
    font-size: 9.5rem;
    font-family: $font-family-title;
    line-height: 1em;
    color: $Blizzard-blue-base;
  }
  .error-issue {
    font-size: 4.5rem;
    line-height: 5rem;
    font-family: var(--font-family);
  }
  .error-button-row {
    margin-top: 2em;
  }
  .button-content {
    font-size: 1.5em;
  }
  a,
  .link {
    color: $color-text-priority-1;
    &:hover,
    &:visited {
      color: $color-text-priority-3;
    }
  }

  a.btn {
    &:hover,
    &:visited {
      color: $color-text-priority-1;
    }
  }
}

.page-not-found-topics {
  display: flex;
  border-top: $border-style-thin $color-border-light;
  border-bottom: $border-style-thin $color-border-light;
  padding: 2em 0;
  .popular-topics,
  .recent-topics {
    width: 45%;
    h2 {
      @include typography-heading-h3;
    }
  }
  .recent-topics {
    margin-left: auto;
  }
  @include small-browser-width {
    flex-wrap: wrap;
    .popular-topics,
    .recent-topics {
      width: 100%;
    }
    .recent-topics {
      margin-top: 2em;
    }
  }
}

.page-not-found-search {
  width: 35%;
  margin: 0 auto;
  text-align: center;
  @include small-browser-width {
    width: 100%;
  }
  padding: 2em 0;
  h2 {
    @include typography-heading-h3;
  }
  #google-search {
    @include flex(none);
    input {
      margin: 0 $space-medium 0 0;
    }
  }
}

// Covers an account auth error
#simple-container {
  width: 100%;
  background-color: transparent;
  .alert.alert-error {
    .alert-title {
      @include typography-heading-h1;
      font-weight: bold;
      color: $Blizzard-blue-base;
    }
    p {
      color: $color-text;
      font-size: 1em;
    }
    background: transparent;
    border-bottom: $border-style-thin $color-border-light;
    text-align: center;
    color: $Blizzard-blue-base;
    font-family: $font-family-title;
    font-size: 2rem;
    line-height: 1.5;
    padding: 2em 0 5em 0;
    @include small-browser-width {
      padding: $space-medium 0 3em 0;
    }

    a,
    .link {
      color: $color-text-priority-1;
      &:hover,
      &:visited {
        color: $color-text-priority-3;
      }
    }
  }
}

// Covers "couldn't load that topic" error
.topic-error {
  line-height: 1.5;
  @include small-browser-width {
    text-align: center;
  }
  .topic-retry {
    display: flex;
  }
}
