#suggested-topics {
  // Message at the bottom of the topic view page that shows "Want to read more? Browse other topics in <category name> or
  // view latest topics."
  .suggested-topics-message {
    text-align: center;
    a {
      @include clickable-item(
        $color: $Blizzard-blue-base,
        $colorActive: $Blizzard-blue-base
      );
    }
    // Category name.
    .badge-wrapper {
      margin: 0;
      font-size: $font-0;
      display: inline-flex;

      // Reduce the spacing between the icon and the text.
      .svg-icon {
        margin-right: 0;
      }
    }
  }

  .main-link {
    .topic-statuses {
      margin-right: 0.5em;
    }
    @include small-browser-width {
      .title {
        width: 80%;
      }
    }
  }

  @include small-browser-width {
    .topic-list th,
    .topic-list td {
      max-width: 260px; // Limits suggested-topic length so the table doesn't overflow on small devices
    }
  }

  h3 {
    margin-bottom: $space-small;
    @include small-browser-width {
      padding: 0 0 0 0.75em;
      &.suggested-topics-message {
        padding: 1em;
      }
    }
  }

  .topic-item-stats {
    margin-top: 0;
  }
  .topic-statuses {
    margin-right: $space-small;
  }
  .topic-post-badges {
    margin-left: $space-small;
  }
  .topic-list-item .main-link a.title {
    padding: 0;
  }
  .topic-list th,
  .topic-list td {
    @include small-browser-width {
      padding: 12px;
    }
  }
}
