@if $franchise == "d2r" {
  // D2R Variables
  $color-theme-primary: #ff6f0b;
  $color-theme-background: #1e0f0d;

  $welcome-banner-bg: url($blizzard-banner-d2r-glass);
  $welcome-banner-bg-color: #000000;
  $welcome-banner-bg-mobile: url($blizzard-banner-d2r-mobile);
  $welcome-banner-text-color: #ddeeff;

  $theme-bg: url($blizzard-background-d2r-top);
  $theme-bg-repeat: url($blizzard-background-d2r-repeat);
  $theme-logo: url($blizzard-logo-d2r-game);
  $theme-logo-height: 240px;
  $theme-small-logo: url($blizzard-logo-d2r-small);
