// Default button. Uses the secondary styling.
.discourse-no-touch .btn,
.btn {
  @include button-secondary;
}

// Primary button.
.discourse-no-touch .btn-primary,
.btn-primary {
  @include button-primary;
}

// Flat button. Resembles a link.
.discourse-no-touch .btn-flat,
.btn-flat {
  @include button-flat;
}

.btn-flat .btn-danger,
.btn-danger {
  &,
  &:focus {
    @include button-danger;
  }
}

// Buttons that have no text, only an icon. We override the padding from Blizzard UI so that the icons will be square
// instead of rectangular.
.discourse-no-touch .btn-danger,
.btn-danger,
.discourse-no-touch .btn-icon,
.btn-icon,
.discourse-no-touch .btn-icon,
.btn-icon {
  // Default small button size padding.
  @include left-right-padding(7px);
}

// OK and cancel buttons, used to accept/cancel changes in some places.
.discourse-no-touch .btn {
  &.ok,
  &.cancel {
    color: $color-text-priority-2;

    &:hover {
      color: $color-text;
    }

    &:active {
      filter: brightness(0.5);
      color: $color-text;
    }
  }

  &.ok:active {
    background-color: $success;
  }

  &.cancel:active {
    background-color: $danger;
  }
}

// A class used by some buttons. Not exactly sure which buttons use it, but at least the Delete Category button on the
// edit category modal does.
.btn.disable-no-hover:hover {
  background-color: $color-button-tertiary-background-hover;
  color: $color-button-tertiary-text-hover;
}

.user-card-button {
  @include button-secondary;
  justify-content: center;
  svg {
    font-size: $font-up-1;
  }
  .d-icon + .d-button-label {
    color: $color-button-tertiary-text;
  }
}

.d-button-label {
  @include text-truncate-ellipsis;
}
