// User settings page.
.user-main {
  @include compensate-padding;
  margin-top: 3.75em;
  // Legend showing the helpful flags, flagged posts, deleted posts, suspensions, and warnings.

  .compose-pm {
    display: none;
  }
  .staff-counters {
    // Don't show the staff counters
    &:blank {
      display: none;
    }

    @include flex("none");
    background-color: rgba(0, 0, 0, 0.25);
    color: $color-text-priority-3;

    a {
      @include flex;
      color: $color-text-priority-2;

      &:hover {
        color: $color-text;
      }

      &:active {
        color: $color-text-priority-3;
      }
    }

    > div,
    div a {
      color: $color-text-priority-3;
      @include flex;
      padding: 8px 0;

      span {
        width: auto;
        height: auto;
        display: inline-flex;
        justify-content: center;
        align-items: center;
      }
    }
  }

  .user-profile-avatar {
    // Ensure the stats aren't glued to the user's avatar
    margin-bottom: 1em;

    &.selectable {
      // Move the right margin to the top div
      margin: 0 20px 20px 0;
      img.avatar {
        margin: 0;
      }
      // Shows a pointer when you can change your avatar
      cursor: pointer;
      // Hover icon size & color
      font-size: 30px;
      color: $brand-primary;
      // Center & hide pencil icon
      .d-icon {
        display: none;
        @include position-centered;
      }
    }

    &.selectable:hover {
      // Darkens the avatar when hovering
      opacity: $opacity-heavy;
      // Show the pencil icon when hovering
      .d-icon {
        display: block;
      }
    }
  }

  // Another wrapper.
  .about {
    // When the info is collapsed so that it only shows the user name, avatar, and the buttons on the right.
    overflow: visible;
    img.avatar {
      box-shadow: 0 0 0 1px rgba(255, 255, 255, 0.3), 0 0 0 2px #000;
    }

    &.collapsed-info {
      // Yet another wrapper.
      .details {
        padding-bottom: 0.5em;
        border-bottom: none;

        .primary {
          align-items: center;
        }
      }
    }

    // When the info is not supposed to have a background.
    &.no-background {
      background: none;
      .details {
        background: none;
      }
    }

    // When the info is collapsed and has a background.
    &.collapsed-info.has-background .details {
      padding: 12px 15px;
    }

    h3 {
      @include text-with-icon($iconSize: 1em);
    }

    // Yet another wrapper.
    .details {
      // Avatar image.
      img.avatar {
        margin-bottom: 0;
      }

      // User name.
      .primary h1.username,
      .primary .full-name {
        @include text-with-icon($iconPosition: "after");
        font-weight: normal;
        width: 100%;
      }

      h2.username {
        background-color: yellow;
        display: none;
      }

      // Suspended user
      .suspended .d-icon-ban {
        display: inline;
        top: 0.175em;
      }
    }

    // Buttons to the right of the user name.
    .controls {
      order: 1;
      padding: 0;
      width: auto;
      max-width: 15em;

      // Parent element for the buttons.
      ul {
        @include flex("none");
        flex-direction: column;

        // Element for each button.
        li {
          @include text-with-icon;
          .user-notifications > summary > .select-kit-header-wrapper {
            justify-content: flex-start;
            .selected-name .d-icon + .name {
              margin-left: 0.28em;
            }
          }
          & > .user-notifications-dropdown,
          a,
          button,
          li {
            width: 100%;
            justify-content: normal;
          }
          &.show-notes-on-profile .d-icon {
            margin-right: 0.65em;
          }
        }
      }
    }

    // Bar at the bottom of an expanded profile showing user stats.
    .secondary {
      background: rgba(0, 0, 0, 0.25);
      border: none;

      dl {
        padding: 8px;
        @include flex;
        flex-wrap: wrap;
      }
    }
  }

  .topic-list-item.bookmark-list-item .link-top-line {
    display: inline;
  }
  .user-content .d-icon-heart {
    color: $color-theme-blizzard !important;
  }
}

.user-content,
.user-main .user-content {
  background: none;
}

// Choose Character modal
.character-selector {
  margin-bottom: 1em;

  .no-results {
    margin: 1em 0 0 1em;
  }

  .character-filter {
    margin-left: 1em;
    display: flex;
    align-items: center;

    .d-icon {
      font-size: $font-up-1;
      margin-right: 0.25em;
      display: inline-block;
      color: $brand-primary;
    }

    input {
      font-size: $font-up-1;
      width: 100%;
    }

    input,
    input:hover,
    input:active,
    input:focus {
      border: none;
      margin: 0;
      padding: 0;
      background-color: transparent;
    }
  }

  .selectable-characters {
    // Resize the modal to show 3.5 rows of characters
    max-height: 450px;

    display: flex;
    flex-wrap: wrap;

    .character-details {
      .character-achievement-points {
        display: none;
      }

      .secondary-details,
      .character-class {
        display: block;
      }

      .character-guild {
        text-transform: uppercase;
        font-size: $font-down-1;
      }

      .character-classic {
        display: block;
      }
    }

    .selectable-character {
      vertical-align: top;
      flex: 0 0 33%;
      width: 400px;
      display: flex;
      padding: 1em;
      border: 1px solid transparent;
      cursor: pointer;

      .character-name a {
        color: inherit;
      }

      .avatar {
        margin-right: 1em;
        @include avatar-regular-glow-only;
      }

      &:hover {
        border: 1px solid $brand-primary;
        background-color: rgba($brand-primary, $opacity-extralight);
      }
    }
  }
}

.user-post-names-outlet > span {
  margin-right: 0.5em;
}

.user-post-names-outlet {
  .character-details {
    font-size: $font-down-1;

    display: flex;
    .secondary-details {
      margin-left: 0.5em;
    }
  }
}

.pref-avatar-blizzard {
  .character {
    display: flex;
    align-items: center;
    margin-bottom: 1em;
  }
}

.character-name {
  font-size: $font-up-1;
  font-weight: bold;
}

.character-guild {
  color: rgba($primary, $opacity-heavy);
}

.character-achievement-points {
  color: rgba($primary, $opacity-heavy);
