.names,
.full-name {
  .d-icon-shield {
    display: none;
  }
}

.cooked,
.user-stream-item p {
  a {
    color: $color-text-priority-3;
    text-decoration: underline;
    &:hover {
      color: $color-text-priority-1;
    }
    &:active {
      color: $color-text-priority-2;
    }
    &:visited {
      color: $color-text-priority-4;
    }
  }

  &.distinguished-text-marking {
    a {
      color: $color-theme-developer-link;
      &:hover {
        color: $color-theme-developer-link-hover;
      }
    }
  }
}

.cloaked-post .distinguished-post-wrapper-marking {
  display: none;
}

.topic-post {
  padding-left: 10px;
  .cooked {
    line-height: 1.6;
  }

  .character-details {
    font-size: $font-down-1;
    .character-guild {
      font-size: $font-down-1;
    }
    .secondary-details {
      font-size: $font-down-1;
    }
  }

  .topic-body,
  .topic-body:before {
    clear: unset;
  }

  .post-info.edits a.wiki {
    color: $brand-primary;
  }

  .post-info.edits a {
    @include flex;
    @include clickable-item($color: $color-text-priority-3);
    .d-icon {
      margin-left: 0.25em;
    }
  }

  .next-tracked-post {
    @include button-secondary;
    padding: $medium-space;
    margin-left: 0.5em;
  }

  // Quote area for quoted posts.
  .quote {
    @include quote-box;
    padding: $medium-space;

    // Name of person getting quoted and the quote itself.
    .title,
    blockquote {
      background: none;
      border: none;
      margin-bottom: 0;
    }

    // Name of person getting quoted.
    .title {
      color: $color-text;
      padding: $medium-space $medium-space 0;

      // Highlight the chevron when the title is highlighted. Clicking anywhere in the title will expand/collapse the
      // quoted post.
      &:hover .d-icon-chevron-down,
      &:hover .d-icon-chevron-up {
        color: $color-text;
      }

      // Avatar image of person getting quoted.
      .avatar {
        @include avatar-regular-glow(
          $quote-avatar-image-width,
          $quote-avatar-image-height,
          $quote-avatar-margin-right
        );
      }

      // Quote controls to expand/collapse quoted post and jump up to the quoted post.
      .quote-controls {
        @include flex;
        // Push the quote controls to the upper right corner of the quote box.
        order: 1;
        margin-left: auto;
        color: $color-text-priority-2;

        .svg-icon {
          transition: $transition-hover-link;
        }

        // "Go to the quoted post" button.
        .back .svg-icon {
          @include clickable-item($color: $color-text-priority-4);
          font-size: $font-down-1;
        }
      }
    }
  }

  .row {
    @include compensate-padding;
  }

  @include small-browser-width {
    .topic-meta-data {
      margin-left: 60px;
      .reply-to-tab {
        // What this post is in reply to
        @include flex(vertical);
        position: unset;
        margin-left: auto;
        svg {
          margin-right: $small-space;
        }
      }
    }

    .embedded-posts.bottom {
      // Nested replies to this post
      @include quote-box;

      // On mobile don't show the topic avatar in nested replies
      .topic-avatar {
        display: none;
      }

      // If the topic body isn't 100% sized the arrow appears in the wrong place
      .topic-body {
        width: 100%;
      }

      // Align the meta data nicely
      .topic-meta-data {
        margin: 0 0 1em 0;
        display: flex;
        justify-content: space-between;

        .post-info,
        .names {
          margin: 0;
        }
        .post-link-arrow {
          @include flex;
          .arrow {
            @include clickable-item;
          }
        }
      }

      padding: $large-space 0;
      .row {
        @include flex(none);
        flex-wrap: wrap;
      }
    }
  }

  .post-links {
    .d-icon-link {
      margin-right: 0.5em;
    }
  }
}

// Quotes created using "> some quote here", and code blocks created using either ``` or by indenting 4 spaces.
blockquote,
pre code {
  @include quote-box;
}

// Quotes created using "> some quote here"
blockquote {
  padding: $medium-space 0.7em;
  span.bml-quote-date {
    margin-right: $medium-space;
  }
}

// This was set with a default 1px width for some reason, and rendering a black line
.select-kit .select-kit-offscreen {
  width: 0;
}

.post-hidden:hover {
  opacity: 1;
}

// In case a blue poster quotes a regular user, we want their text to stay the correct color
.quote.no-group {
  blockquote {
    p,
    .expanded-quote {
      color: $color-text-priority-2;
    }
  }
}
.post-notice {
  display: none;
}
