// Small actions are an audit log of actions done on a post, and only shows for admin users. They will be displayed
// below the last post in the post view.
.small-action {
  @include flex;
  margin-right: $timeline-spacing; // Reserve space for the timeline.
  max-width: unset; // Remove the max-width. The margin-right handles this.
  // Change the top border to be a bottom border.
  border-top: none;
  border-bottom: $border-style-thin $color-border-light;
  @include compensate-padding;
  @include small-browser-width {
    margin-right: 0;
  }

  // The "8 days later" that will display between posts made days apart.
  &.time-gap {
    border-bottom: none;
    @include small-browser-width {
      border-top: $border-style-thin $color-border-light;
      .topic-avatar {
        display: none;
      }
    }

    // If the next sibling is an audit log item, show a top border.
    + .onscreen-post {
      border-top: $border-style-thin $color-border-light;
    }
  }

  // Icon indicating the mod action performed.
  .topic-avatar {
    align-self: center;
    padding: 0;
    width: auto;
    margin: 0;
    .d-icon {
      color: $color-theme-primary;
      font-size: $font-0;
    }
  }

  // Description for the small action.
  .small-action-desc {
    @include flex;
    margin-top: 0;
    flex: 1;
    padding-top: 0.6em;
    padding-bottom: 0.6em;
    padding-left: 11px; // Make the left padding the same as the topic body's padding so that they line up.
    color: $color-text-priority-3;
    .custom-message {
      margin: 0 0.5em;
      p {
        line-height: 1.2;
      }
    }
    &.timegap {
      color: $color-text-priority-3;
    }
    @include small-browser-width {
      &.timegap {
        padding-left: 0;
      }
    }
    // Text for the small action. Remove the padding to vertically center the text and flex to the remaining space.
    > p {
      padding-top: 0;
      flex: 1;
      line-height: 1em;
    }
  }

  .btn.small-action-edit,
  .btn.small-action-delete {
    @include button-flat;
    padding: 5px;
  }

  // Edit button in the small actions.
  .small-action-edit {
    order: 1;
  }

  // Delete button in the small actions.
  .small-action-delete {
    order: 2;
  }
  &.topic-post-visited .topic-post-visited-line {
    border-color: $brand-primary;
    .topic-post-visited-message {
      color: $brand-primary;
    }
  }
}

.gap {
  @include small-browser-width {
    padding: 1em;
  }
}
