.topic-avatar {
  width: 85px;
  border-top: none;
  @include very-small-browser-width {
    // Supporting slightly smaller desktop viewports
    width: 50px;
  }
  // Avatar image parent div.
  .main-avatar {
    @include flex("both");
    float: left;

    // Avatar image itself.
    .avatar {
      @include avatar-regular-glow(
          $post-avatar-image-width,
          $post-avatar-image-height,
          $post-avatar-margin-right
        )
        @include small-browser-width {
        @include avatar-regular-glow(
          $post-avatar-image-width-small,
          $post-avatar-image-height-small,
          $post-avatar-margin-right
        );
      }
    }
  }

  .username,
  .full-name {
    font-size: $font-0;
  }
}

// IP address and additional displayed on a post for admin users.
.staff-info {
  margin-top: 0.5em;
  color: $color-text-priority-3;
  font-size: $font-down-1;
  clear: both;
  display: flex;
  flex-wrap: wrap;
  width: 100%;

  div {
    display: inline-block;
    background-color: $color-text-priority-5;
    border-radius: 6px;
    padding: 0 0.5em;
    margin-top: 0.25em;
    margin-right: 0.25em;
    @include user-select(all);

    a {
      color: $color-text-priority-3;

      &:hover {
        color: $color-text-priority-1;
      }
    }
  }

  .created-ip-address {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }
}
