// A post item in a topic list.
.topic-list-item {
  cursor: pointer;

  // Checkbox for the bulk actions mode. To show it, click on the bulk actions button to the left of the "Topic" header.
  .bulk-select input[type="checkbox"] {
    min-height: unset; // Remove the min-height: 40px that Blizzard UI sets so that it doesn't affect the row height.
  }

  // First line showing the topic name. It's possible for a topic to show a short excerpt of the post below it (and thus
  // becoming the second line), but I don't think we'll ever use that feature.
  .link-top-line {
    display: flex;
    align-items: center;
  }

  // Make the thumb tack icon inherit its color from the parent instead of hard-coded to a gray color.
  .d-icon-thumbtack,
  .d-icon-thumbtack.unpinned {
    color: inherit;
  }

  .first-tracked-post {
    margin-right: 0.3em;
  }

  // Topic status icons, also displays in front of the post in the topic list.
  .topic-statuses {
    color: $color-theme-icon;
    font-size: $font-up-1;
  }

  // Topic status icon.
  .topic-status {
    color: inherit;
    font-size: inherit;

    .d-icon {
      margin-right: 0.3em;
    }
  }

  // Remove the padding around the topic name and change its size and color.
  .main-link {
    font-size: inherit;

    // Topic name's text.
    a.title {
      @include text-nowrap-truncate-ellipsis;
      @include clickable-item($color: $color-text-priority-2);
      padding: 0;
    }
  }

  // Visited posts (posts that have already been viewed). The class name needs to be this specific in order to override
  // Discourse's style.
  &.visited {
    a.title:not(.badge-notification) {
      @include clickable-item($color-text-priority-3);
    }
    .topic-statuses {
      svg {
        color: $color-text-priority-3;
      }
    }
  }

  // Make the category name column inherit the font size.
  .category .badge-wrapper {
    font-size: inherit;
    display: inline-block;
  }

  // Vertically align the chat icon with the reply count next to it.
  .num.posts .badge-posts {
    @include text-with-icon($marginAmount: 0.3em, $iconSize: 1em);
    padding: 0;
  }

  // Blue chat icon in front of the reply count.
  .blizzard-reply-icon {
    color: #00b4ff;
  }

  // For topic lists that show the likes column. Currently this is disabled and only shows up in the styleguide.
  .likes a {
    @include text-with-icon;
  }

  // Age of the post.
  .age {
    text-align: right;
  }

  .assigned-to {
    margin-left: 2.8em;
  }
}

// Visited link color for anon visitors
.topic-list-main-link {
  .anon & {
    a.title:visited:not(.badge-notification) {
      @include clickable-item($color-text-priority-3);
    }
  }
}

// Element showing what actions have been reported on a post (e.g. "flagged as spam").
.post-action {
  margin: 0;

  // Parent element for the user icon and the descriptive text.
  .clearfix {
    @include flex;
    line-height: 0;
  }

  // User icon.
  .avatar {
    margin-right: 0.3em;
  }

  &.deleted-post {
    @include flex;
    justify-content: flex-end;
    .description,
    .d-icon {
      margin-right: 0.5em;
    }
  }
}

// Vertical bar that appears in front of a topic when pressing j and k for keyboard navigation.
.topic-list tr.selected td:first-child,
.topic-list-item.selected td:first-child,
.topic-post.selected {
  box-shadow: -3px 0 0 $color-theme-primary; // We're only overriding the color, but need to copy over the entire style.
}
