// Buttons at the bottom of an individual post, whether it's the OP or a reply.
.topic-post nav.post-controls {
  @include flex("none");

  // Parent element for the buttons.
  .actions {
    @include flex("none");
    @include small-browser-width {
      flex-wrap: wrap; // Admins on small devices might need icons to wrap, otherwise they might become inaccessible.
    }
    margin-left: auto;
  }

  .btn.like-count {
    @include button-flat;

    &:hover {
      background: transparent;
      color: $color-button-flat-text-hover;
    }
    svg {
      margin-right: 0;
    }
  }

  .post-admin-menu {
    width: 220px;
  }

  .bookmark.bookmarked {
    color: $color-theme-primary;
  }

  .extra-buttons {
    display: contents;
  }

  button.widget-button {
    @include button-flat;
    margin-left: 1.5em;
    font-size: $font-0;

    &:disabled {
      color: $color-theme-blizzard;
      .svg-icon {
        opacity: 0.5;
      }

      &:hover {
        color: $color-theme-blizzard;
      }
    }

    &.btn.create {
      .d-button-label {
        margin-left: 0.5em;
      }
    }

    .d-button-label {
      color: $color-theme-blizzard;
      &:hover {
        color: $color-button-text-hover;
      }
    }
  }

  // Show Replies button. Only displays for posts that have direct replies to them.
  button.show-replies {
    @include text-with-icon($marginAmount: 0.5em, $iconPosition: "after");
    margin-left: 0;

    // Text inside the button.
    .d-button-label {
      color: $color-text-priority-2;
      transition: $transition-hover-button;
    }

    &:hover .d-button-label {
      color: $color-button-text-hover;
    }

    &:active .d-button-label {
      color: $color-button-text-pressed;
    }
  }

  @include small-browser-width {
    margin: $large-space 0;
  }

  // Like count and flag count should be next to their respective icons
  .double-button button.widget-button:not(:first-child) {
    margin-left: 0;
  }

  // Fix like hover
  .double-button button.button-count + .toggle-like.d-hover {
    background: none;
  }

  // Make flag badge round, give the badge count some room
  .double-button button.button-count:not(.my-likes) {
    &,
    &.has-pending {
      padding-right: 6px;
      span {
        padding: 0.1em 0.5em;
      }
    }
  }
  .actions .double-button:hover button {
    background: none;
    color: $color-button-flat-text-hover;
  }
}

// Element that shows the names of those that liked the post. Only shows if the like count is clicked.
.who-liked {
  @include flex;
  justify-content: flex-end;
  flex-wrap: wrap;
  @include small-browser-width {
    justify-content: flex-start;
    margin-left: 0;
  }

  // Each user that liked the post.
  a {
    margin-bottom: 0;
  }

  // Avatar image for each user.
  .avatar {
    display: block;
  }
}

.discourse-no-touch .topic-body .actions .fade-out {
  opacity: 1; // Remove the 0.7 opacity that Discourse adds to the reply button when an individual post is not hovered.
  transition: $transition-hover-button; // Use Blizzard's button transition instead of Discourse's.
}
