.b-realms-list {
  margin-top: 4em;

  @include breakpoint(mobile-extra-large) {
    margin-top: 0;
    margin-bottom: 2em;
  }

  .realms-title {
    @include flex;
    align-items: center;
    padding-bottom: 0.25em;
    cursor: pointer;

    .caret-icon {
      font-size: $font-down-3;
      font-weight: bold;
      color: rgba(255, 255, 255, 0.5);
      transition: color 200ms;
      margin-left: 1em;
    }

    @include breakpoint(mobile-extra-large) {
      padding-left: 1.5em;
      border-bottom: 0;
    }

    .realms-icon {
      width: 30px;
      height: 30px;
      margin-right: 0.5em;
      background-image: url($blizzard-wow-globe);
      background-size: 30px 30px;
      @include breakpoint(mobile-extra-large) {
        margin-right: 1em;
      }
    }
    .realms-title-text {
      font-size: $font-up-1;
      font-weight: bold;
      text-transform: uppercase;
    }
    border-bottom: $border-style-thin $color-border-primary;
  }

  .filtered-realms {
    @include flex;
    align-items: flex-start;
    margin-top: 0.3em;
    flex-wrap: wrap;

    // the font size for your realms is slightly bigger
    &.yours {
      .b-realm {
        .realm-name {
          font-size: $font-down-1;
        }
      }
    }

    .filtered-realm {
      @include flex;
      width: 25%;
      @include breakpoint(mobile-extra-large) {
        width: 100%;
      }
    }

    .b-realm {
      @include flex;
      align-items: flex-start;
      margin-bottom: 1em;

      .realm-name {
        font-size: $font-down-2;
        color: $color-text-priority-1;
        &:hover {
          text-decoration: underline;
        }
      }

      @include breakpoint(mobile-extra-large) {
        @include b-transbox;
        min-height: 44px;
        width: 100%;
        align-items: center;
        margin-bottom: 0;
        padding: 0 2em;
        .realm-name {
          font-size: $font-0;
          padding: 0.25em 0;
        }
      }

      .realm-icon {
        margin-top: 4px;
        width: 15px;
        height: 15px;
        min-width: 15px;
        min-height: 15px;
        background-image: url($blizzard-wow-globe);
        background-size: 15px 15px;

        @include breakpoint(mobile-extra-large) {
          width: 16px;
          height: 16px;
          margin-right: 0.8em;
        }
        margin-right: 0.5em;
      }
    }
  }
}
