// Although .list-controls is only a wrapper div for other components, there are a number of styles that is prefixed
// with it and messes up the layout of the components. We need to undo those styles here.
.list-controls {
  margin: 0;

  // Fix the padding for the items in the dropdown menu.
  .category-dropdown-menu .badge-wrapper.none {
    @include list-item-dropdown;
  }

  .nav {
    float: none;
    margin-right: 0;
  }
}

// Inner wrapper for all the navigation components, which includes the current category name, new topic button, topic
// filter links, and some additional buttons.
// TODO: The styleguide uses .navigation-container, whereas the actual Discourse instance uses .category-navigation.
// Figure out the reason for this difference.
.category-navigation,
.navigation-container {
  @include compensate-padding;
  @include flex("none");
  flex-wrap: wrap;
  width: 100%;
  box-sizing: border-box;

  // Make the buttons slightly larger. This will make the size somewhere between medium and large.
  .btn {
    font-size: $font-up-1;
  }

  // Edit category button, only shows for admin users.
  .edit-category {
    @include text-with-icon;
    @include button-secondary;
    justify-content: center;
    height: 100%;
    @include small-browser-width {
      order: 3;
    }
  }

  // Use custom padding for dropdown select in discovery areas
  .select-kit.dropdown-select-box {
    .dropdown-select-box-header {
      font-size: $font-up-1;
      height: 100%;
      padding: 0.45em 0.75em;
      margin-left: 0.4em;
      .d-icon.d-icon-d-tracking,
      .d-icon.d-icon-d-watching {
        color: $Blizzard-blue-base;
      }
    }
  }

  .navigation-controls {
    @include small-browser-width {
      width: 100%;
    }
  }
  #create-topic {
    @include button-primary;

    @include small-browser-width {
      flex-grow: 1;
      margin: 0;
      justify-content: center;
    }

    // Always show the label (Discourse hides it on mobile)
    span.d-button-label {
      display: inline;
    }

    // Move the plus icon down one pixel to better align with the text.
    .svg-icon {
      margin-top: 1px;
    }

    .d-button-label {
      margin-left: 0.2em;
    }
  }
}

#navigation-bar,
.user-primary-navigation {
  li {
    font-family: var(--heading-font-family);
  }
}
