// Quote button. Displays when you highlight some text in an individual topic view.
.quote-button.visible {
  @include b-box;
  padding: 5px;
  button {
    @include text-with-icon;
    // Use the same background color as the b-box instead of removing it on hover.
    @include clickable-item($background: $color-form-dropdown-background-base);
    opacity: 1;
    padding: 5px;
  }
}
