#reply-control {
  @include b-box($gradient: true);
  z-index: z("header"); // Bumping above embed content

  .characters-required {
    right: 1.4em;
  }
  &.closed {
    border: none;
  }

  .compose-admin-message {
    background-color: $color-background-lighter;
    padding: 0 $space-small;
    margin: $space-tiny 0;
    border-radius: 2px;
    font-size: $font-down-1;
    font-weight: $font-weight-normal;
  }

  .title-input {
    input {
      box-shadow: none;
      border: $border-style-thin $color-form-border-base;
      height: $header-height;
    }
  }
  .d-editor-textarea-wrapper {
    border: $border-style-thin $color-form-border-base;
    textarea {
      border: none;
    }
  }
  .d-editor-button-bar {
    background: rgba($color-form-dropdown-background-base, 0.75);
    border-bottom: $border-style-thin $color-form-border-base;
    padding: 5px;
    margin: 0;
    .btn {
      align-items: center;
      border: none;
      justify-content: center;
      padding: 0 $medium-space;
      color: var(--primary-medium);
      background: transparent;
      @include small-browser-width {
        padding: 0 $small-space;
      }
    }
  }
  @include large-browser-width {
    .d-editor-preview-wrapper,
    .d-editor-preview {
      background: transparent;
    }
    .d-editor-preview-wrapper {
      border: 1px dashed $color-form-border-base;
      padding: $large-space;
      margin-top: 0;
    }
  }
  textarea {
    line-height: 1.5;
  }
  #reply-title {
    line-height: normal;
  }
  .category-input {
    height: $header-height;
    margin-bottom: 0.313em;
    @include small-browser-width {
      margin-left: 0;
    }
  }
  .select-kit-header {
    height: 100%;
  }
  .category-chooser .select-kit-row {
    line-height: 2;
    padding: $space-small;
    .category-status {
      line-height: 1;
      span.badge-category {
        font-weight: bold;
      }
    }
    &.is-selected {
      .category-status .badge-wrapper,
      .category-desc,
      .topic-count {
        color: $secondary;
      }
    }
    .category-desc {
      width: 100%;
      @include text-nowrap-truncate-ellipsis;
      line-height: 1.25;
      display: block;
    }
  }
  .reply-to {
    @include flex(vertical);
    margin-top: 0;
    a {
      margin: 0 0.15em;
    }
  }
  .toggler {
    font-size: $font-0;
    @include clickable-item($color-text-priority-3);
  }
  .toggle-toolbar {
    @include clickable-item($color-text-priority-3);
  }
  #draft-status {
    line-height: 1;
  }
  @include very-small-browser-width {
    // Reduces vertical padding on very small mobile screens
    .create {
      padding: 7px;
    }
  }
  &.draft {
    @include b-box($gradient: true);
    color: $primary;
    .draft-text {
      @include flex(vertical);
      svg {
        margin-right: $small-space;
      }
    }
  }
  .grippie {
    background: transparent;
    &:before {
      border-color: $color-form-border-base;
    }
  }
  @include small-browser-width {
    .composer-bottom-right {
      align-self: center;
    }
    .composer-controls {
      display: flex;
      align-items: center;
    }
  }
  @include large-browser-width {
    .submit-panel {
      margin: $space-small 0;
    }
    .save-or-cancel {
      a.cancel {
        margin-left: $space-medium;
      }
    }
  }
  #file-uploading {
    align-items: center;
  }

  .title-input .popup-tip {
    bottom: 100%;
    white-space: nowrap;
  }
}
.upload-selector {
  // Upload modal in composer
  .radios {
    display: flex;
    margin-bottom: 0.5em;
    @include small-browser-width {
      flex-wrap: wrap;
    }
    label.radio {
      margin: 0.25em 0.5em;
      @include small-browser-width {
        margin-top: 1em;
      }
    }
  }
  #filename-input {
    padding: 0.5em;
  }
}
.emoji-picker {
  font-size: $font-0;
  @include b-box($gradient: true);
  .filter {
    input {
      margin-left: $space-small;
    }
    .d-icon-search {
      color: $primary;
      font-size: $font-0;
    }
  }
  .categories-column {
    overflow-y: hidden;
  }
  .diversity-scale {
    svg {
      display: none;
    }
    &.selected {
      svg {
        display: block;
        color: $secondary;
      }
    }
  }
}
.d-editor {
  .d-editor-modal {
    @include b-box($gradient: true);
    padding: $medium-space;
    h3 {
      font-size: $font-0;
    }
  }
}
.select-kit.dropdown-select-box.toolbar-popup-menu-options .select-kit-header {
  background-color: transparent;
  color: $color-button-tertiary-text;
  &:hover {
    color: $color-button-tertiary-text-hover;
  }
}
