.category-boxes {
  @include flex;
  align-items: flex-start;

  .subcategory-container {
    width: 33.33%;

    &.third-1 {
      padding: 0 0.5em;
      .b-subcategory {
        margin: 0 auto;
      }
    }

    &.third-2 .b-subcategory {
      margin-left: auto;
    }

    @include breakpoint(mobile-extra-large) {
      width: 100%;

      &:third-1,
      &:third-2 {
        padding: 0;
        .b-subcategory {
          margin: 0 0 -1px 0;
        }
      }
    }
  }
}
