$menu-panel-padding: 0.5em;

.menu-panel {
  padding: $menu-panel-padding;
  // .menu-panel has 0.5em padding, and we want the HR to go edge to edge, so add equal negative margin
  hr {
    margin-left: calc(-#{$menu-panel-padding} + 1px);
    margin-right: calc(-#{$menu-panel-padding} + 1px);
  }

  div.menu-links-header .menu-links-row {
    button {
      &,
      &:hover,
      &:active {
        background: none;
        border: 1px solid transparent;
        color: inherit;
        border-bottom: 1px solid $color-theme-primary;
        margin-top: 0px;
        & .svg-icon {
          color: var(--primary-med-or-secondary-med);
        }
      }
    }
  }

  .menu-container-footer-links li {
    font-size: $font-down-1;
  }
}

// Common class for the dropdown menus for the search, hamburger and user icons.
.d-header .menu-panel {
  @include b-box($gradient: true);
  box-sizing: content-box;

  &.drop-down {
    margin-top: -4px;
  }

  .badge-category {
    max-width: none;
  }

  // Category submenu
  li.category-link {
    &,
    span {
      overflow: hidden;
    }
  }
}

// Search menu. Opened by clicking the search icon on the upper right of the page.
.search-menu {
  // Parent element for the searh text input field and searching spinner.
  .search-input {
    position: relative;
  }

  // Search text input field.
  #search-term {
    height: auto;
    margin: 0;
    min-height: auto;
    line-height: 1.2;
    padding: 7px;
    font-size: $font-0;
  }

  // Parent element for the searching spinner.
  .searching {
    top: 50%;
    right: 0.7em;
    transform: translateY(-50%);

    // Actual spinner image.
    .spinner {
      // Slow down the animation because the size is reduced, otherwise it moves too fast.
      @include spinner-size(20px, 1.2s);
    }
  }

  // "Search the <insert name> category" label with the checkbox.
  .search-context {
    @include flex;
    padding: 2px 5px;

    .show-help {
      padding-left: 0.5em;
    }
    label {
      display: inline-block;
      cursor: pointer;
      color: $color-text-priority-2;
      text-overflow: ellipsis;
      overflow: hidden;

      // The checkbox for the label.
      input {
        min-height: auto;
        margin: 2px 0.2em 0 0;
        color: $color-text-priority-2;
      }
    }

    // Options link on the lower right when the above checkbox is unchecked.
    .widget-link {
      margin-left: auto;
      line-height: normal;
      @include clickable-item;
      color: $color-text-priority-2;
    }
  }

  // Found results.
  .results {
    margin-top: 5px;
  }

  // Remove the background color when hovering a search result. There's already a hover effect on the topic title.
  li:not(.category):not(.heading):hover a:not(.badge-notification) {
    background: none;
  }

  // Wrapper element for a single search result.
  .search-link {
    @include clickable-item($color: $color-theme-blizzard);
    @include clickable-item(
      $element: ".blurb",
      $colorHover: $color-text-priority-2,
      $colorActive: $color-text-priority-5
    );
    @include clickable-item(
      $element: ".badge-wrapper.none",
      $color: $color-text-priority-2
    );

    // Wrapper for the topic statuses, topic name, and topic category.
    .topic {
      @include flex;
      margin-bottom: 0.2em;
      .topic-title {
        @include text-nowrap-truncate-ellipsis;
      }
    }

    .topic-statuses:not(:empty) {
      display: inline-flex;
      margin-right: 0.2em;
      font-size: $font-down-2; // Make the icon sizes slightly larger.
    }

    // Topic blurb. The first few lines of the topic.
    .blurb {
      font-size: $font-down-1;
      line-height: inherit;
    }
  }
}

// Hamburger menu. Opened by clicking the hamburger icon on the upper right of the page.
.hamburger-panel .menu-panel {
  // Each menu item in the hamburger menu.
  .widget-link,
  .d-link {
    @include clickable-item;
    // Change the top and bottom padding from 0.25em to 0.3em so that there's a bit more space between each menu item.
    color: $color-text-priority-2;
    padding-top: 0.3em;
    padding-bottom: 0.3em;
  }

  // Flagged post badge
  .badge-notification {
    display: flex;
    margin-left: 0.4em;
    padding: 0.25em 0.45em;
  }

  // Links below the FAQ link.
  .menu-links li a {
    @include text-with-icon;
  }

  // Hide switching from Desktop to Mobile
  .menu-links .mobile-toggle-link {
    display: none;
  }
}

// User menu. Opened by clicking the avatar icon on the upper right of the page.
.user-menu .notifications .spinner {
  // Slow down the animation because the size is reduced, otherwise it moves too fast.
  @include spinner-size(20px, 1.2s);
}
.user-menu .menu-panel {
  a,
  button {
    // button elements that appear in this menu (for better accessibility support) should be styled as links
    font-family: var(--font-family);
    font-size: $font-down-1;
  }

  // Header containing the username and the 3 icons on the right.
  .menu-links-header {
    display: block;

    li:first-child {
      overflow: hidden;
    }
  }

  .quick-access-panel li,
  .quick-access-panel li:not(.show-all) {
    border: 0px;
    background: transparent;
    @include flex;

    a,
    button {
      @include flex;
      transition: $Navbar-transition-hover-link;
      color: $color-text-priority-2;
      width: calc(100% - 1em); // Subtracting the width of the icons
      &:hover {
        color: $color-text-priority-1;

        .d-icon {
          color: $Blizzard-blue-hover;
        }
      }
      div {
        @include text-nowrap-truncate-ellipsis;
      }

      p {
        font-size: $font-0;
        text-overflow: ellipsis;
      }

      .d-icon {
        transition: $Navbar-transition-hover-link;
        margin-right: 0.5em;
        color: $brand-primary;
      }
    }

    .selection-indicator {
      @include selection-indicator;
    }

    &.read .selection-indicator {
      visibility: hidden;
    }
  }

  .notifications li.read {
    background: transparent;
  }

  // Inner wrapper for the menu header items.
  .menu-links-row {
    @include flex;
  }

  // User name with generic icon.
  .user-activity-link {
    @include text-with-icon;
    margin: 0;
    max-width: none;
    padding: 0.375em;

    // User name text.
    .d-label {
      @include text-nowrap-truncate-ellipsis;
    }
  }

  // 3 buttons on the upper right.
  .glyphs {
    @include flex("none");
    margin-left: auto;
  }

  // Common class for the user name and the 3 buttons to the right of it.
  .widget-button {
    @include clickable-item;
    color: $color-text-priority-2;
  }

  // Button to show all notifications
  .panel-body-bottom .btn,
  .widget-button {
    display: flex;
    justify-content: center;
    @include button-secondary;
  }

  hr.bottom-area {
    margin-top: 0.5em;
  }

  // When user can't change their avatar
  .user-menu-bottom-without-avatar {
    // Button to dismiss notifications
    .dismiss-link {
      display: block;
      float: none;
      .menu-links {
        @include flex;
      }
      li {
        width: 100%;
        margin-top: $space-small;
        @include button-secondary;
        margin-top: 0.3em;
        padding: 7px 0;
        justify-content: center;
        a {
          padding: 0;
          font-size: $font-0;
        }
      }
    }
  }

  // When user can change their avatar
  .user-menu-bottom-with-avatar {
    hr.bottom-area {
      margin-bottom: 0.5em;
    }

    // Buttons to 'Change Avatar/Character' and 'Dismiss' notifications
    .dismiss-link,
    .select-avatar-link,
    .select-character-link {
      .d-icon {
        color: $brand-primary;
        margin-right: 0.5em;
      }
    }

    // Change Avatar/Character button
    .select-avatar-link,
    .select-character-link {
      float: left;

      .select-avatar,
      .select-character {
        @include flex;
      }
    }
  }

  // Logout link. Make it look like a primary button.
  .logout {
    @include button-primary;
    @include flex("horizontal");
    margin-top: $space-small;
  }
  .widget-link.dismiss > .d-icon {
    color: $color-theme-blizzard;
  }
}
.menu-panel li.heading a.categories-link:hover,
div.menu-links-header .menu-links-row li a:not(.active):hover,
.menu-panel li a.widget-link:hover,
.menu-panel li a.widget-link:focus {
  background: none;
  color: $color-text-priority-1;
}
.menu-panel li a.widget-link .d-icon:hover {
  color: $color-text-priority-3;
}
