// Base colors for the icons, name, and desc.
@mixin select-box-row-base {
  background: none;

  .icons {
    color: $color-text-priority-2;
  }

  // Brighten up the name and description.
  .texts {
    .name {
      color: $color-text-priority-2;
      padding-bottom: 0.2em;
    }

    span.desc {
      color: $color-text-priority-3;
      font-size: $font-0;
    }
  }
}

// Hover colors for the icons, name, and desc.
@mixin select-box-row-hover {
  background-color: $color-form-dropdown-background-hover;

  .icons {
    color: $color-text;
  }

  .texts {
    .name {
      color: $color-text;
    }

    .desc {
      color: $color-text-priority-2;
    }
  }
}

/*
There are 3 types of select boxes:

1. dropdown-select-box: dropdown where the selected item looks like an icon button.
2. combo-box: dropdown where the selected item looks like an input field and has a search feature.
3. multi-combo-box: same as 2, but allows multiple items to be selected.
*/

// Common class for all 3 types of select boxes.
.select-kit {
  // Parent element for all the items in the dropdown list.
  .select-kit-collection {
    @include styled-scrollbar;
    max-height: unset !important; // Let it auto-size, there's already a max-height set on the parent.
    background: none;
  }
  .select-kit-row {
    .name {
      // Overriding default Discourse line-height
      line-height: 1.2;
    }
  }
}

// Dropdown where the selected item looks like an icon button.
.select-kit.dropdown-select-box {
  // Parent element for the icon button.
  .dropdown-select-box-header {
    height: auto;
    @include button-secondary; // Style it as a Blizzard button.

    // If there's a caret up or caret down icon to the right of another icon, make the caret icon smaller.
    .d-icon {
      + .d-icon-blizzard-chevron_up,
      + .d-icon-blizzard-chevron_down {
        font-size: $font-down-2;
      }
    }

    // Change the border color on the button when it's clicked.
    &.is-focused {
      box-shadow: none;

      // If the button is focused and hovered, change the border from a dark blue to a light blue.
      &:hover {
        border-color: $color-form-border-active;
      }
    }
  }

  // Parent element for the dropdown items.
  .select-kit-body {
    @include b-box($gradient: true);
    max-height: $dropdown-max-height;
  }

  &.is-expanded {
    .select-kit-body {
      max-width: 450px;
    }
  }

  // An individual dropdown item.
  .select-kit-row {
    @include left-right-padding(0.6em);
    @include select-box-row-base;
    font-size: $font-0;

    // Don't use the :hover style, use &.is-highlighted instead because the current selection can be changed using the
    // keyboard keys while the mouse is hovering over another item.
    &:hover {
      background: none;
    }

    // This item is the previously-selected item before the dropdown was opened.
    &.is-selected {
      color: $color-form-text-active;
      background: none; // Hide the background color and show the selection indicator instead.

      .selection-indicator {
        visibility: visible;
      }
    }

    // This item is highlighted.
    &.is-highlighted {
      @include select-box-row-hover;
    }

    // Blue dot showing the currently-selected item.
    .selection-indicator {
      @include selection-indicator;
      margin: 0.75em 0.25em 0 0;
      visibility: hidden;
    }

    .icons {
      .d-icon {
        color: inherit;
        font-size: $font-0;
        margin: 0.175em 0.5em 0 0;
      }
    }

    // Parent element for the item name and its descriptive text.
    .texts {
      line-height: normal;

      .name {
        font-weight: normal;
      }

      .desc {
        font-size: $font-down-1;
      }
    }

    /*
    If this is the only item in the list, hide the selection indicator and remove the is-selected and is-highlighted
    styling, and add in a :hover styling. This is because the item will always have .is-selected and .is-highlighted, so
    we need to remove them and add a :hover styling instead to get it to change.
    */
    &:only-child {
      .selection-indicator {
        display: none;
      }

      // Revert back to the base styling.
      &.is-highlighted,
      &.is-selected {
        @include select-box-row-base;
      }

      // Add the hover styling.
      &:hover {
        @include select-box-row-hover;
      }
    }
  }
}

// Dropdown where the selected item looks like an input field and has a search feature.
.select-kit.combo-box,
.select-kit.multi-select {
  background: none;
  // Field that shows the selected item.
  .select-kit-header {
    border: $border-style-thin $color-form-border-base;
    background-color: $color-form-background-base;
    border-radius: 0;
    font-size: $font-0;
    font-weight: $font-weight-form-base;
    line-height: $line-height-form-base;
    color: $color-form-text-base;
    text-decoration: none;
    text-overflow: ellipsis;
    text-align: left;
    overflow: hidden;
    white-space: nowrap;
    padding: 0 $space-medium 0 $space-small;
    width: 100%;

    padding-right: 0.6em; // Move the dropdown caret icon closer to the right edge.
    transition-duration: $transition-hover-speed; // Use the hover transition speed, which is slightly faster.
    padding: $medium-space;
    line-height: normal;

    // Dropdown arrow icon.
    .caret-icon {
      color: $color-theme-blizzard;
      transition: opacity $transition-hover-speed;
    }

    // When the header is hovered, but not focused. Border should go from medium gray to light gray.
    &:hover {
      border-color: $color-text-priority-3;

      // Make the dropdown arrow icon full opacity on hover.
      .caret-icon {
        opacity: 1;
      }
    }

    // When the header is focused. This occurs when the header is clicked on and clicked off for the first time (it
    // stays focused until a second click, or if another focusable element is clicked). Border is blue in this case.
    &.is-focused {
      border-radius: 0;
      box-shadow: none;

      // When the header is focused and hovered. This occurs when the header is clicked
      // off but still in focus and the user hovers over the header. We need to use a
      // different border color than the :hover styling, otherwise the border goes
      // from dark blue to gray, making it look like it's getting de-emphasized.
      &:hover {
        border-color: $color-form-border-active;
      }
    }
  }

  // Search filter input that appears when you start typing with the dropdown open.
  .select-kit-filter {
    border-top: none;
    border-bottom-color: $dropdown-inset-color;
    flex-shrink: 0;
    padding: 0 $medium-space;

    // Input field for the search filter.
    .select-kit-filter-input {
      // Make the input take up the entire space of the parent element and give it a
      // background gradient.
      padding: 0 2em 0 0.65em; // Leave enough right padding for the magnifying glass icon.
      margin-right: 0;
      background: $Navbar-gradient-background-dropdown-linear;
      font-size: $font-down-1;

      &:hover,
      &:focus {
        color: $color-text;
      }
    }

    // Magnifying glass icon.
    .filter-icon {
      position: absolute;
      right: 0.7em;
      font-size: $font-down-1;
    }
  }

  // A single dropdown item.
  .select-kit-row {
    @include list-item-dropdown;
    margin: 0;
    padding: $medium-space 0.75em;

    // Don't use the hover styling from .ListItem--dropdown. Reset it back to the default style and apply the hover
    // styling through &.is-highlighted instead. This is because items can be changed using the keyboard while
    // the mouse is hovering over another item.
    &:hover {
      color: $color-form-dropdown-text-base;
      background: none;
    }

    // Highlighted item. This occurs when the item is either hovered or selected through the keyboard arrows keys.
    &.is-highlighted {
      color: $color-form-text-active;
      background-color: $color-form-dropdown-background-hover;
    }

    // Selected item. This is the currently-selected item.
    &.is-selected {
      color: $color-form-text-active;
      background-color: $color-theme-blizzard;
    }
  }

  // When the dropdown is open.
  &.is-expanded {
    // Make the selected item field display as if it's focused, with brighter text and a full opacity dropdown arrow.
    .select-kit-header {
      border-color: $color-form-border-active;
      color: $color-form-text-active;

      .caret-icon {
        opacity: 1;
      }
    }

    // Parent element for the search filter input and the dropdown items.
    .select-kit-body {
      @include b-box($gradient: true);
      max-height: $dropdown-max-height;
      border-radius: 0;
      width: auto;
    }

    // Element that's absolutely positioned behind the header and dropdown to make it look like one element. We use a
    // different styling, so we'll just hide it.
    .select-kit-wrapper {
      display: none;
    }
  }
  &.category-chooser {
    height: $header-height;
    .combo-box-header {
      height: 100%;
      padding: 0.25em 0.556em;
    }
  }
}
