.distinguished-post-wrapper-marking {
  height: 100%;
  width: 100%;
  //Added for when parent has padding
  overflow: hidden;
  position: absolute;

  // TODO: remove all references to distinguished-post-* items
  // This is happening in pure CSS, and we are removing the markup from the plugin
  display: none;
}

.distinguished-post-marking {
  opacity: 0.1;
  transform: rotate(-15deg);
  fill: $brand-primary;
  margin-top: 8px;
}

.desktop-view .topic-post .names .user-title,
.names .user-title a {
  font-size: $font-down-1;
}

// All of the below classes have the same styling:
// Blue text, blue glow, with the blizzard watermark
// logo behind the post content.
$blizzard-staff-classes: community, cs-moderator, cs-tech, cs-support-sse,
  cs-engagement, cs-manager, community-manager, support, support-analyst,
  support-manager, tech, quality-assurance, qa-heroes, forum-developer,
  starcraft-developer, sc2-developer, sc2-gamesite, d3-developer, developer,
  api_users;

@each $blizzard-staff-class in $blizzard-staff-classes {
  @include theme-distinguished-classes(
    $blizzard-staff-class,
    $brand-primary,
    $color-theme-moderator-link,
    $color-theme-moderator-link-hover,
    $blizzard-watermark-logo
  );
}

@include theme-distinguished-classes("cs-mvp", $brand-mvp);
@include theme-distinguished-classes(
  "CommunityCouncil",
  $brand-community-council
);
